.button {
  background: gold;
  border: 0;
  outline: 0;
  border-radius: 3px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  cursor: pointer;
  padding: 15px;
}

.disabled {
  background: gray;
  color: white;
}

.button:not(.disabled):not(.selected):hover {
  background: goldenrod;
}

.selected {
  background: greenyellow;
  color: black;
}



